import { InputLabel } from "@material-ui/core";
import LoginImg from "assets/images/auth/forgott.png";
import clsx from "clsx";
import Button from "components/shared/Button";
import InlineLoader from "components/shared/InlineLoader";
import TextField from "components/shared/formik/TextField";
import { useUserContext } from "contexts/AllContexts";
import { Formik } from "formik";
import { useState } from "react";
import { useNavigate, useSearchParams } from "react-router-dom";
import { toast } from "react-toastify";
import { postRequest } from "utils/api";
import * as yup from "yup";
import style from "./ForgotPassword.module.scss";

export default function ForgotPassword() {
  const [formData, setFormData] = useState({});
  const [isRemember, setIsRemember] = useState(false);
  const [isIncorrectCreds, setIsIncorrectCreds] = useState(false);
  const [passwordType, setPasswordType] = useState("password");
  const [isLoading, setIsLoading] = useState(false);
  const [isErrorUsername, setIsErrorUsername] = useState(false);

  const userContext = useUserContext();
  const navigate = useNavigate();

  const [searchParams] = useSearchParams();

  function handleClickLogin() {
    navigate("/auth/login");
  }

  function changeFormData(e) {
    let { name, value } = e.target;

    if (name === "rememberMe") setIsRemember(e.target.checked);
    else {
      if (name === "username") {
        setIsErrorUsername(false);
        value = value.replace(/\s/g, "");
      }

      setFormData((prev) => ({ ...prev, [name]: value }));
    }
  }

  const validationSchema = yup.object().shape({
    email: yup.string().email().required("Please enter your registered email!"),
  });

  function handleClickEye() {
    setPasswordType((prev) => (prev === "password" ? "text" : "password"));
  }

  function submitForm(values) {
    setIsLoading(true);

    postRequest(
      "/accounts/forgot_password",
      { email: values.email },
      { noAuth: true },
    )
      .then((res) => {
        toast.success(
          "Success! Your new password has been sent to your email.",
          { icon: false },
        );
        navigate("/auth/login");
      })
      .catch((err) => {
        console.error(err);
        toast.error("Something went wrong! Please try again.", { icon: false });
      })
      .finally(() => setIsLoading(false));
  }

  const [isOpenForgetPassModal, setIsOpenForgetPassModal] = useState(false);
  function toggleForgetPassModal() {
    setIsOpenForgetPassModal((prev) => !prev);
  }

  const [emailForgotPass, setEmailForgotPass] = useState("");
  const [errorEmailForgotPass, setErrorEmailForgotPass] = useState("");
  const [apiResponseForgotPass, setApiResponseForgotPass] = useState({
    isActive: false,
    isError: false,
    message: "",
  });

  function handleChangeFormForgotPass(e) {
    const { value } = e.target;
    setEmailForgotPass(value);
  }

  function handleSubmitFormForgotPass(e) {
    e.preventDefault();
    const validationSchema = yup.object().shape({
      email: yup
        .string()
        .email("*Must be a valid email")
        .max(255)
        .required("*Email is required"),
    });

    try {
      validationSchema.validateSync(
        { email: emailForgotPass },
        { abortEarly: false },
      );
      setIsLoading(true);
      setErrorEmailForgotPass({});
      setApiResponseForgotPass({
        isActive: false,
        isError: false,
        message: "",
      });
      setErrorEmailForgotPass({});

      postRequest(
        `/accounts/forgot_password`,
        { email: emailForgotPass },
        { noAuth: true },
      )
        .then((resp) => {
          if (resp.data.status) {
            setApiResponseForgotPass({
              isActive: true,
              isError: false,
              message: "A new password has been sent to you email.",
            });
          } else {
            setApiResponseForgotPass({
              isActive: true,
              isError: true,
              message: "No account found with given email id.",
            });
          }
        })
        .catch((err) => {
          console.log(err);
          setApiResponseForgotPass({
            isActive: true,
            isError: true,
            message: "An error occured. Please try again",
          });
        })
        .finally(() => {
          setIsLoading(false);
        });
    } catch (err) {
      let errs = {};

      err.inner.forEach((er) => {
        errs[er.path] = er;
      });

      console.log(err);
      setErrorEmailForgotPass(errs);
    }
  }

  return (
    <div className={style.wrapper}>

      <div className={style.inner}>
        <div className={style.left}>
          <img src={LoginImg} alt="" />
        </div>
        <div className={style.right}>
          <div className={style.headerContainer}>
            {/* <div className={style.logoContainer}>
              <img src={Logo} alt="" width={200}/>
            </div> */}
            <div className={style.loginButton}>
              Remembered? &nbsp; Back to{" "}
              <span onClick={handleClickLogin}>&nbsp; Login</span>
            </div>
            <div className={style.text}>Forgot Your Password?</div>
            <div className={style.subtext}>
              No worries. Just enter your registered email, and we'll guide you
              through the process.
            </div>
          </div>

          <Formik
            initialValues={{
              email: "",
            }}
            validationSchema={validationSchema}
            onSubmit={submitForm}
          >
            {({ handleSubmit }) => (
              <form
                onChange={handleChangeFormForgotPass}
                onSubmit={handleSubmit}
              >
                <div className={style.row}>
                  <InputLabel htmlFor="username" className={style.label}>
                    Enter your registered email
                  </InputLabel>

                  <TextField
                    className={style.textField}
                    name="email"
                    label="email"
                    placeholder="Email"
                    type="email"
                    required
                  />
                  <p>
                    {errorEmailForgotPass.email &&
                      errorEmailForgotPass.email.message}
                  </p>
                </div>
                <div
                  className={clsx(
                    style.apiResponse,
                    apiResponseForgotPass.isError ? style.error : style.success,
                  )}
                >
                  {apiResponseForgotPass.isActive &&
                    apiResponseForgotPass.message}
                </div>

                <div className={style.btnContainer}>
                  <Button
                    submit
                    disabled={isLoading}
                    options={{ width: "299px", height: "55px" }}
                  >
                    {isLoading ? (
                      <InlineLoader size={20} color="white" />
                    ) : (
                      "Reset Password"
                    )}
                  </Button>
                </div>
              </form>
            )}
          </Formik>
        </div>
      </div>
    </div>
  );
}
